.services__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}

.services {
  background: var(--color-bg-variant);
  border-radius: 2rem 2rem 2rem 2rem;
  border: 1px solid var(--color-primary);
  height: fit-content;
  transition: var(--transition);
}
.services:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}
.services__head {
  background: var(--color-primary);
  padding: 1rem;
  border-radius: 2rem 2rem 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(o, 0, 0, 0.2);
}
.services__head h3 {
  color: var(--color-bg);
  font-size: 1rem;
  text-align: center;
}
.service__list{
    padding:1rem;
    margin-top: 1rem;
}
.service__list li{
    display: flex;
    gap:1rem;
    margin-bottom: 0.8rem;
}
.service__list-icon{
    font-size: 4rem;
    color:var(--color-primary);
    margin-top: 0.2rem;
}

.service__list p{
    font-size: 0.9rem;
}

/* --- Media Query / Media Device */

@media screen and (max-width: 1024px) {
    .services__container{
        grid-template-columns: 1fr 1fr;
        gap:2rem;
    }
    .service{
        height: auto;
    }
  
}

@media screen and (max-width: 600px) {
   
    .services__container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }  
}
